import React from 'react'

const Avalanche = ({ width = 37, height = 37, ...rest }) => {
  return (
    <svg
      width={width}
      height={height}
      viewBox='0 0 37 37'
      xmlns='http://www.w3.org/2000/svg'
      {...rest}
    >
      <rect x='0' y='0' width='37' height='37' fill='url(#pattern2)' />
      <defs>
        <pattern
          id='pattern2'
          patternContentUnits='objectBoundingBox'
          width='1'
          height='1'
        >
          <use href='#image0_214_494' transform='scale(0.00390625)' />
        </pattern>
        <image
          id='image0_214_494'
          width='256'
          height='256'
          href='data:image/png;base64,'
        />
      </defs>
    </svg>
  )
}

export default Avalanche
